// Animations module
export function initializeAnimations() {
  console.log('üé¨ Initializing animations module...')
  
  // Initialize scroll-triggered animations
  initializeScrollAnimations()
  
  // Initialize hover animations
  initializeHoverAnimations()
  
  // Initialize page transition animations
  initializePageTransitions()
  
  // Initialize loading animations
  initializeLoadingAnimations()
  
  console.log('‚úÖ Animations module initialized')
}

function initializeScrollAnimations() {
  // Modern Intersection Observer for better performance
  const observerOptions = {
    threshold: 0.1,
    rootMargin: '0px 0px -50px 0px'
  }
  
  const observer = new IntersectionObserver((entries) => {
    entries.forEach(entry => {
      if (entry.isIntersecting) {
        const element = entry.target
        const animationType = element.dataset.animation || 'fadeInUp'
        const delay = element.dataset.delay || 0
        
        setTimeout(() => {
          animateElement(element, animationType)
        }, delay)
        
        observer.unobserve(element)
      }
    })
  }, observerOptions)
  
  // Observe elements with animation data attributes
  const animatedElements = document.querySelectorAll('[data-animation]')
  animatedElements.forEach(el => {
    el.style.opacity = '0'
    observer.observe(el)
  })
  
  // Legacy WOW.js support
  if (typeof WOW !== 'undefined') {
    const wow = new WOW({
      boxClass: 'wow',
      animateClass: 'animated',
      offset: 0,
      mobile: true,
      live: true
    })
    wow.init()
  }
}

function initializeHoverAnimations() {
  // Enhanced button animations
  const buttons = document.querySelectorAll('.btn, .button')
  buttons.forEach(button => {
    button.addEventListener('mouseenter', () => {
      if (!button.disabled) {
        button.style.transform = 'translateY(-2px)'
        button.style.boxShadow = '0 4px 12px rgba(0,0,0,0.15)'
      }
    })
    
    button.addEventListener('mouseleave', () => {
      button.style.transform = 'translateY(0)'
      button.style.boxShadow = ''
    })
    
    button.addEventListener('mousedown', () => {
      button.style.transform = 'translateY(0) scale(0.98)'
    })
    
    button.addEventListener('mouseup', () => {
      button.style.transform = 'translateY(-2px) scale(1)'
    })
  })
  
  // Card hover effects
  const cards = document.querySelectorAll('.card, .card-hover')
  cards.forEach(card => {
    card.addEventListener('mouseenter', () => {
      card.style.transform = 'translateY(-5px)'
      card.style.boxShadow = '0 10px 25px rgba(0,0,0,0.15)'
    })
    
    card.addEventListener('mouseleave', () => {
      card.style.transform = 'translateY(0)'
      card.style.boxShadow = ''
    })
  })
  
  // Image hover effects
  const images = document.querySelectorAll('.img-hover')
  images.forEach(img => {
    img.addEventListener('mouseenter', () => {
      img.style.transform = 'scale(1.05)'
      img.style.filter = 'brightness(1.1)'
    })
    
    img.addEventListener('mouseleave', () => {
      img.style.transform = 'scale(1)'
      img.style.filter = 'brightness(1)'
    })
  })
}

function initializePageTransitions() {
  // Smooth page loading
  window.addEventListener('load', () => {
    document.body.classList.add('page-loaded')
    
    // Animate main content
    const mainContent = document.querySelector('main, .main-content')
    if (mainContent) {
      animateElement(mainContent, 'fadeIn')
    }
  })
  
  // Form submission animations
  const forms = document.querySelectorAll('form')
  forms.forEach(form => {
    form.addEventListener('submit', () => {
      const submitBtn = form.querySelector('button[type="submit"]')
      if (submitBtn) {
        animateElement(submitBtn, 'pulse')
      }
    })
  })
  
  // Modal animations
  const modals = document.querySelectorAll('.modal')
  modals.forEach(modal => {
    modal.addEventListener('show.bs.modal', () => {
      const modalDialog = modal.querySelector('.modal-dialog')
      if (modalDialog) {
        modalDialog.style.animation = 'modalSlideIn 0.3s ease-out'
      }
    })
    
    modal.addEventListener('hide.bs.modal', () => {
      const modalDialog = modal.querySelector('.modal-dialog')
      if (modalDialog) {
        modalDialog.style.animation = 'modalSlideOut 0.3s ease-in'
      }
    })
  })
}

function initializeLoadingAnimations() {
  // Loading spinner for AJAX requests
  let loadingCount = 0
  
  // Show loading indicator
  function showLoading() {
    loadingCount++
    if (loadingCount === 1) {
      let loader = document.querySelector('.global-loader')
      if (!loader) {
        loader = createLoader()
        document.body.appendChild(loader)
      }
      loader.style.display = 'flex'
      animateElement(loader, 'fadeIn')
    }
  }
  
  // Hide loading indicator
  function hideLoading() {
    loadingCount--
    if (loadingCount <= 0) {
      loadingCount = 0
      const loader = document.querySelector('.global-loader')
      if (loader) {
        animateElement(loader, 'fadeOut', () => {
          loader.style.display = 'none'
        })
      }
    }
  }
  
  // Intercept fetch requests for loading states
  const originalFetch = window.fetch
  window.fetch = function(...args) {
    showLoading()
    return originalFetch.apply(this, args)
      .finally(() => {
        setTimeout(hideLoading, 300) // Minimum loading time for UX
      })
  }
  
  // Lazy loading for images
  const images = document.querySelectorAll('img[data-src]')
  const imageObserver = new IntersectionObserver((entries) => {
    entries.forEach(entry => {
      if (entry.isIntersecting) {
        const img = entry.target
        img.src = img.dataset.src
        img.classList.add('loaded')
        imageObserver.unobserve(img)
      }
    })
  })
  
  images.forEach(img => imageObserver.observe(img))
}

function animateElement(element, animationType, callback) {
  // Remove existing animation classes
  element.classList.remove('animated', 'fadeIn', 'fadeOut', 'slideInUp', 'slideInDown', 'bounce', 'pulse')
  
  // Add animation classes
  element.classList.add('animated', animationType)
  element.style.opacity = '1'
  
  // Handle animation end
  const handleAnimationEnd = () => {
    element.classList.remove('animated', animationType)
    element.removeEventListener('animationend', handleAnimationEnd)
    if (callback) callback()
  }
  
  element.addEventListener('animationend', handleAnimationEnd)
}

function createLoader() {
  const loader = document.createElement('div')
  loader.className = 'global-loader'
  loader.innerHTML = `
    <div class="loader-content">
      <div class="spinner">
        <div class="double-bounce1"></div>
        <div class="double-bounce2"></div>
      </div>
      <p class="loader-text">ƒêang t·∫£i...</p>
    </div>
  `
  
  // Add loader styles
  const style = document.createElement('style')
  style.textContent = `
    .global-loader {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(255, 255, 255, 0.9);
      display: flex;
      justify-content: center;
      align-items: center;
      z-index: 9999;
      backdrop-filter: blur(3px);
    }
    
    .loader-content {
      text-align: center;
    }
    
    .spinner {
      width: 40px;
      height: 40px;
      position: relative;
      margin: 0 auto 20px;
    }
    
    .double-bounce1, .double-bounce2 {
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background-color: var(--primary-color, #007bff);
      opacity: 0.6;
      position: absolute;
      top: 0;
      left: 0;
      animation: sk-bounce 2.0s infinite ease-in-out;
    }
    
    .double-bounce2 {
      animation-delay: -1.0s;
    }
    
    @keyframes sk-bounce {
      0%, 100% { 
        transform: scale(0.0);
      } 50% { 
        transform: scale(1.0);
      }
    }
    
    .loader-text {
      color: var(--gray-600, #6c757d);
      margin: 0;
      font-size: 14px;
    }
    
    @keyframes modalSlideIn {
      from {
        opacity: 0;
        transform: translateY(-50px) scale(0.8);
      }
      to {
        opacity: 1;
        transform: translateY(0) scale(1);
      }
    }
    
    @keyframes modalSlideOut {
      from {
        opacity: 1;
        transform: translateY(0) scale(1);
      }
      to {
        opacity: 0;
        transform: translateY(-50px) scale(0.8);
      }
    }
    
    .page-loaded {
      animation: pageLoadFade 0.5s ease-out;
    }
    
    @keyframes pageLoadFade {
      from {
        opacity: 0;
        transform: translateY(20px);
      }
      to {
        opacity: 1;
        transform: translateY(0);
      }
    }
    
    img[data-src] {
      opacity: 0;
      transition: opacity 0.3s;
    }
    
    img.loaded {
      opacity: 1;
    }
  `
  
  if (!document.querySelector('#vite-animations-styles')) {
    style.id = 'vite-animations-styles'
    document.head.appendChild(style)
  }
  
  return loader
}

// Smooth scrolling utility
export function smoothScrollTo(target, offset = 0) {
  const element = typeof target === 'string' ? document.querySelector(target) : target
  if (element) {
    const top = element.offsetTop - offset
    window.scrollTo({
      top: top,
      behavior: 'smooth'
    })
  }
}

// Animation performance optimization
export function optimizeAnimations() {
  // Reduce animations on low-performance devices
  if (navigator.hardwareConcurrency <= 2) {
    document.documentElement.style.setProperty('--animation-duration', '0.1s')
  }
  
  // Respect user preferences
  if (window.matchMedia('(prefers-reduced-motion: reduce)').matches) {
    document.documentElement.style.setProperty('--animation-duration', '0.01ms')
  }
} 