// Components module
export function initializeComponents() {
  console.log('üß© Initializing components module...')
  
  // Initialize enhanced form components
  initializeFormComponents()
  
  // Initialize carousel improvements
  initializeCarousels()
  
  // Initialize modal enhancements
  initializeModals()
  
  // Initialize navigation improvements
  initializeNavigation()
  
  // Initialize utility components
  initializeUtilities()
  
  console.log('‚úÖ Components module initialized')
}

function initializeFormComponents() {
  // Enhanced file upload
  const fileInputs = document.querySelectorAll('input[type="file"]')
  fileInputs.forEach(input => {
    enhanceFileInput(input)
  })
  
  // Auto-resize textareas
  const textareas = document.querySelectorAll('textarea[data-auto-resize]')
  textareas.forEach(textarea => {
    setupAutoResize(textarea)
  })
  
  // Character counter
  const inputsWithCounter = document.querySelectorAll('[data-max-length]')
  inputsWithCounter.forEach(input => {
    setupCharacterCounter(input)
  })
}

function initializeCarousels() {
  // Enhanced Owl Carousel if available
  if (typeof jQuery !== 'undefined' && jQuery.fn.owlCarousel) {
    jQuery('.owl-carousel').each(function() {
      const $carousel = jQuery(this)
      const options = {
        loop: true,
        margin: 10,
        nav: true,
        dots: true,
        responsive: {
          0: { items: 1 },
          600: { items: 2 },
          1000: { items: 3 }
        },
        autoplay: $carousel.data('autoplay') !== false,
        autoplayTimeout: $carousel.data('timeout') || 5000,
        autoplayHoverPause: true,
        smartSpeed: 800
      }
      
      $carousel.owlCarousel(options)
    })
  }
}

function initializeModals() {
  // Enhanced modal functionality
  const modals = document.querySelectorAll('.modal')
  modals.forEach(modal => {
    // Auto-focus first input when modal opens
    modal.addEventListener('shown.bs.modal', () => {
      const firstInput = modal.querySelector('input:not([type="hidden"]), select, textarea')
      if (firstInput) {
        setTimeout(() => firstInput.focus(), 100)
      }
    })
  })
}

function initializeNavigation() {
  // Smooth scroll for anchor links
  const anchorLinks = document.querySelectorAll('a[href^="#"]')
  anchorLinks.forEach(link => {
    link.addEventListener('click', (e) => {
      const targetId = link.getAttribute('href').substring(1)
      const target = document.getElementById(targetId)
      
      if (target) {
        e.preventDefault()
        target.scrollIntoView({
          behavior: 'smooth',
          block: 'start'
        })
      }
    })
  })
}

function initializeUtilities() {
  // Tooltip initialization
  const tooltipTriggerList = document.querySelectorAll('[data-bs-toggle="tooltip"]')
  if (typeof bootstrap !== 'undefined' && bootstrap.Tooltip) {
    tooltipTriggerList.forEach(tooltipTriggerEl => {
      new bootstrap.Tooltip(tooltipTriggerEl)
    })
  }
  
  // Back to top button
  createBackToTopButton()
}

// Enhanced file input
function enhanceFileInput(input) {
  const wrapper = document.createElement('div')
  wrapper.className = 'file-input-wrapper'
  
  const label = document.createElement('label')
  label.className = 'file-input-label'
  label.setAttribute('for', input.id)
  label.innerHTML = `
    <i class="fas fa-cloud-upload-alt"></i>
    <span>Ch·ªçn file ho·∫∑c k√©o th·∫£ v√†o ƒë√¢y</span>
  `
  
  const fileName = document.createElement('div')
  fileName.className = 'file-name'
  
  wrapper.appendChild(label)
  wrapper.appendChild(fileName)
  
  input.parentNode.insertBefore(wrapper, input)
  wrapper.appendChild(input)
  
  input.addEventListener('change', () => {
    if (input.files.length > 0) {
      fileName.textContent = input.files[0].name
      wrapper.classList.add('has-file')
    } else {
      fileName.textContent = ''
      wrapper.classList.remove('has-file')
    }
  })
}

// Auto-resize textarea
function setupAutoResize(textarea) {
  const resize = () => {
    textarea.style.height = 'auto'
    textarea.style.height = textarea.scrollHeight + 'px'
  }
  
  textarea.addEventListener('input', resize)
  resize()
}

// Character counter
function setupCharacterCounter(input) {
  const maxLength = parseInt(input.dataset.maxLength)
  const counter = document.createElement('div')
  counter.className = 'character-counter'
  
  const updateCounter = () => {
    const remaining = maxLength - input.value.length
    counter.textContent = `${remaining} k√Ω t·ª± c√≤n l·∫°i`
    counter.className = `character-counter ${remaining < 10 ? 'warning' : ''}`
  }
  
  input.addEventListener('input', updateCounter)
  input.parentNode.appendChild(counter)
  updateCounter()
}

// Back to top button
function createBackToTopButton() {
  const button = document.createElement('button')
  button.className = 'back-to-top'
  button.innerHTML = '<i class="fas fa-arrow-up"></i>'
  button.setAttribute('aria-label', 'V·ªÅ ƒë·∫ßu trang')
  
  button.addEventListener('click', () => {
    window.scrollTo({ top: 0, behavior: 'smooth' })
  })
  
  window.addEventListener('scroll', () => {
    button.classList.toggle('visible', window.scrollY > 300)
  })
  
  document.body.appendChild(button)
} 