// Main entry point cho Vite
console.log('üöÄ Vite + Spring Boot initialized!')

// Import CSS
import '../css/main.css'

// Import existing functionality
import { initializeValidation } from './modules/validation.js'
import { initializeAnimations } from './modules/animations.js'
import { initializeComponents } from './modules/components.js'

// Wait for DOM to be ready
document.addEventListener('DOMContentLoaded', function() {
  console.log('üìÑ DOM Content Loaded')
  
  // Initialize all modules
  initializeValidation()
  initializeAnimations()
  initializeComponents()
  
  // Hot Module Replacement support
  if (import.meta.hot) {
    import.meta.hot.accept()
    console.log('üî• HMR enabled')
  }
})

// Global error handler
window.addEventListener('error', function(e) {
  console.error('‚ùå Global error:', e.error)
})

// Export cho global access n·∫øu c·∫ßn
window.app = {
  version: '1.0.0',
  initialized: true
} 