// Validation module
export function initializeValidation() {
  console.log('üîç Initializing validation module...')
  
  // Real-time validation for forms
  const forms = document.querySelectorAll('form[data-validate]')
  forms.forEach(form => {
    setupFormValidation(form)
  })
  
  // Password strength indicator
  const passwordInputs = document.querySelectorAll('input[type="password"]')
  passwordInputs.forEach(input => {
    setupPasswordStrength(input)
  })
  
  // Email validation
  const emailInputs = document.querySelectorAll('input[type="email"]')
  emailInputs.forEach(input => {
    setupEmailValidation(input)
  })
  
  console.log('‚úÖ Validation module initialized')
}

function setupFormValidation(form) {
  const inputs = form.querySelectorAll('input, select, textarea')
  
  inputs.forEach(input => {
    // Real-time validation on input
    input.addEventListener('input', debounce(() => {
      validateField(input)
    }, 300))
    
    // Validation on blur
    input.addEventListener('blur', () => {
      validateField(input)
    })
  })
  
  // Form submission validation
  form.addEventListener('submit', (e) => {
    let isValid = true
    
    inputs.forEach(input => {
      if (!validateField(input)) {
        isValid = false
      }
    })
    
    if (!isValid) {
      e.preventDefault()
      showFormErrors(form)
    } else {
      showLoadingState(form)
    }
  })
}

function validateField(input) {
  const value = input.value.trim()
  const type = input.type
  const required = input.hasAttribute('required')
  
  // Clear previous validation
  clearFieldValidation(input)
  
  // Required validation
  if (required && !value) {
    showFieldError(input, 'Tr∆∞·ªùng n√†y kh√¥ng ƒë∆∞·ª£c ƒë·ªÉ tr·ªëng')
    return false
  }
  
  // Type-specific validation
  switch (type) {
    case 'email':
      if (value && !isValidEmail(value)) {
        showFieldError(input, 'Email kh√¥ng ƒë√∫ng ƒë·ªãnh d·∫°ng')
        return false
      }
      break
      
    case 'tel':
      if (value && !isValidPhone(value)) {
        showFieldError(input, 'S·ªë ƒëi·ªán tho·∫°i kh√¥ng ƒë√∫ng ƒë·ªãnh d·∫°ng')
        return false
      }
      break
      
    case 'password':
      if (value && !isValidPassword(value)) {
        showFieldError(input, 'M·∫≠t kh·∫©u ph·∫£i c√≥ √≠t nh·∫•t 8 k√Ω t·ª±, bao g·ªìm ch·ªØ hoa, ch·ªØ th∆∞·ªùng v√† s·ªë')
        return false
      }
      break
  }
  
  // Custom validation attributes
  const pattern = input.getAttribute('pattern')
  if (pattern && value && !new RegExp(pattern).test(value)) {
    const message = input.getAttribute('data-error-message') || 'ƒê·ªãnh d·∫°ng kh√¥ng h·ª£p l·ªá'
    showFieldError(input, message)
    return false
  }
  
  // Min/Max length
  const minLength = input.getAttribute('minlength')
  const maxLength = input.getAttribute('maxlength')
  
  if (minLength && value.length < parseInt(minLength)) {
    showFieldError(input, `T·ªëi thi·ªÉu ${minLength} k√Ω t·ª±`)
    return false
  }
  
  if (maxLength && value.length > parseInt(maxLength)) {
    showFieldError(input, `T·ªëi ƒëa ${maxLength} k√Ω t·ª±`)
    return false
  }
  
  // Show success state
  showFieldSuccess(input)
  return true
}

function setupPasswordStrength(input) {
  const container = input.parentElement
  let strengthIndicator = container.querySelector('.password-strength')
  
  if (!strengthIndicator) {
    strengthIndicator = document.createElement('div')
    strengthIndicator.className = 'password-strength mt-2'
    strengthIndicator.innerHTML = `
      <div class="strength-meter">
        <div class="strength-bar"></div>
      </div>
      <div class="strength-text small text-muted"></div>
    `
    container.appendChild(strengthIndicator)
  }
  
  input.addEventListener('input', () => {
    const strength = calculatePasswordStrength(input.value)
    updatePasswordStrength(strengthIndicator, strength)
  })
}

function calculatePasswordStrength(password) {
  let score = 0
  const feedback = []
  
  // Length check
  if (password.length >= 8) score++
  else feedback.push('√çt nh·∫•t 8 k√Ω t·ª±')
  
  // Lowercase check
  if (/[a-z]/.test(password)) score++
  else feedback.push('Ch·ªØ th∆∞·ªùng')
  
  // Uppercase check
  if (/[A-Z]/.test(password)) score++
  else feedback.push('Ch·ªØ hoa')
  
  // Number check
  if (/\d/.test(password)) score++
  else feedback.push('S·ªë')
  
  // Special character check
  if (/[!@#$%^&*(),.?":{}|<>]/.test(password)) score++
  else feedback.push('K√Ω t·ª± ƒë·∫∑c bi·ªát')
  
  return { score, feedback, total: 5 }
}

function updatePasswordStrength(indicator, strength) {
  const bar = indicator.querySelector('.strength-bar')
  const text = indicator.querySelector('.strength-text')
  const percentage = (strength.score / strength.total) * 100
  
  bar.style.width = `${percentage}%`
  
  if (strength.score <= 2) {
    bar.className = 'strength-bar bg-danger'
    text.innerHTML = `<span class="text-danger">Y·∫øu</span> - C·∫ßn: ${strength.feedback.join(', ')}`
  } else if (strength.score <= 3) {
    bar.className = 'strength-bar bg-warning'
    text.innerHTML = `<span class="text-warning">Trung b√¨nh</span> - C·∫ßn: ${strength.feedback.join(', ')}`
  } else {
    bar.className = 'strength-bar bg-success'
    text.innerHTML = '<span class="text-success">M·∫°nh</span>'
  }
}

function setupEmailValidation(input) {
  input.addEventListener('blur', async () => {
    const email = input.value.trim()
    if (email && isValidEmail(email)) {
      // You can add email existence check here if needed
      checkEmailExists(email, input)
    }
  })
}

async function checkEmailExists(email, input) {
  try {
    const response = await fetch(`/api/check-email?email=${encodeURIComponent(email)}`)
    const data = await response.json()
    
    if (data.exists) {
      showFieldError(input, 'Email n√†y ƒë√£ ƒë∆∞·ª£c s·ª≠ d·ª•ng')
    }
  } catch (error) {
    console.warn('Could not check email existence:', error)
  }
}

// Validation helpers
function isValidEmail(email) {
  const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/
  return emailRegex.test(email)
}

function isValidPhone(phone) {
  // Vietnamese phone number format
  const phoneRegex = /^0[3|5|7|8|9][0-9]{8}$/
  return phoneRegex.test(phone.replace(/\s+/g, ''))
}

function isValidPassword(password) {
  return password.length >= 8 && 
         /[a-z]/.test(password) && 
         /[A-Z]/.test(password) && 
         /\d/.test(password)
}

// UI helpers
function showFieldError(input, message) {
  input.classList.add('is-invalid')
  input.classList.remove('is-valid')
  
  let errorDiv = input.parentElement.querySelector('.invalid-feedback')
  if (!errorDiv) {
    errorDiv = document.createElement('div')
    errorDiv.className = 'invalid-feedback'
    input.parentElement.appendChild(errorDiv)
  }
  
  errorDiv.textContent = message
  errorDiv.style.display = 'block'
}

function showFieldSuccess(input) {
  input.classList.add('is-valid')
  input.classList.remove('is-invalid')
  
  const errorDiv = input.parentElement.querySelector('.invalid-feedback')
  if (errorDiv) {
    errorDiv.style.display = 'none'
  }
}

function clearFieldValidation(input) {
  input.classList.remove('is-valid', 'is-invalid')
  
  const errorDiv = input.parentElement.querySelector('.invalid-feedback')
  if (errorDiv) {
    errorDiv.style.display = 'none'
  }
}

function showFormErrors(form) {
  const firstInvalidField = form.querySelector('.is-invalid')
  if (firstInvalidField) {
    firstInvalidField.focus()
    firstInvalidField.scrollIntoView({ behavior: 'smooth', block: 'center' })
  }
}

function showLoadingState(form) {
  const submitBtn = form.querySelector('button[type="submit"]')
  if (submitBtn) {
    submitBtn.disabled = true
    submitBtn.innerHTML = '<span class="spinner-border spinner-border-sm me-2"></span>ƒêang x·ª≠ l√Ω...'
  }
}

// Utility functions
function debounce(func, wait) {
  let timeout
  return function executedFunction(...args) {
    const later = () => {
      clearTimeout(timeout)
      func(...args)
    }
    clearTimeout(timeout)
    timeout = setTimeout(later, wait)
  }
} 